* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.greyed-out {
  filter: brightness(45%);
}

.closed-modal {
  display: none;
}

.open-modal {
  display: block;
}

ul {
  list-style-type: none;
}

a {
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
}

input {
  caret-color: var(--main-orange);
}

body {
  font-family: var(--font);
  background-color: var(--tertiary-grey);
  width: 100%;
  overflow-x: hidden;
}

#body-wrapper {
  width: 100%;
}

:root {
  --main-orange: #d87d4a;
  --secondary-black: #101010;
  --tertiary-grey: #f1f1f1;
  --light-grey: #fafafa;
  --salmon: #fbaf85;
  --pure-white: #fff;
  --pure-black: #000;
  --font: "Manrope", sans-serif;
}

.heading-3 {
  text-transform: uppercase;
  margin-top: 15vh;
  margin-bottom: 5vh;
  font-size: clamp(21px, 3vw, 27px);
}

#cart-modal {
  background-color: var(--light-grey);
  width: 84vw;
  border-radius: 11px;
  margin-top: 5vh;
  margin-left: 8vw;
  padding: 8vh 8vw 0;
}

#cart-heading {
  text-transform: uppercase;
  grid-area: cart-heading;
  font-size: clamp(18px, 3vw, 23px);
}

#remove-all-items {
  color: gray;
  width: max-content;
  cursor: pointer;
  background-color: #0000;
  border: none;
  grid-area: remove-all;
  justify-self: end;
  font-size: clamp(12px, 3vw, 18px);
  text-decoration: underline;
  position: relative;
  left: 10%;
}

.cart-items-flex-group {
  flex-direction: column;
  grid-area: items-group;
  gap: 3vh;
  display: flex;
}

.cart-item-img {
  width: 20vw;
  border-radius: 11px;
}

.cart-item-name-and-price {
  flex-direction: column;
  align-self: center;
  gap: 1vh;
  display: flex;
  position: relative;
  left: 4%;
}

.cart-item-name {
  width: 30vw;
  font-size: clamp(13px, 3vw, 16px);
}

.cart-item-price {
  color: gray;
  font-weight: bold;
}

.cart-item-flex {
  flex-direction: row;
  display: flex;
}

.cart-counter-btn-container {
  height: 5vh;
  flex-direction: row;
  align-self: center;
  display: flex;
}

.cart-in-and-de-btn {
  color: #999;
  background-color: #f1f1f1;
  border: none;
  font-weight: bold;
}

#cart-decrease-btn {
  padding-left: 3vw;
}

#cart-decrease-btn:hover {
  color: var(--main-orange);
}

.cart-number-box {
  width: 12vw;
  text-align: center;
  height: inherit;
  font-family: var(--font);
  background-color: #f1f1f1;
  border: none;
  font-size: clamp(11px, 3vw, 15px);
  font-weight: bold;
}

#cart-increase-btn {
  padding-right: 3vw;
}

#cart-increase-btn:hover {
  color: var(--main-orange);
}

#cart-total-and-price {
  flex-direction: row;
  grid-area: cart-total-and-price;
  justify-content: start;
  display: flex;
}

#cart-total {
  text-transform: uppercase;
  color: gray;
  font-size: clamp(13px, 3vw, 18px);
}

#cart-total-price {
  font-weight: bold;
}

#checkout-btn {
  width: 100%;
  background-color: var(--main-orange);
  color: var(--pure-white);
  text-transform: uppercase;
  height: 7vh;
  border: none;
  grid-area: checkout-btn;
  align-self: center;
  margin-bottom: 5vh;
  padding-top: .5vh;
  padding-left: 2vw;
  padding-right: 2vw;
  font-size: 13px;
  font-weight: normal;
}

#checkout-btn:hover {
  background-color: var(--salmon);
}

#checkout-link {
  color: var(--pure-white);
  width: inherit;
  height: inherit;
  display: block;
  position: relative;
  top: 25%;
}

#top-nav-bar {
  background-color: var(--secondary-black);
  height: 10vh;
  border-bottom: 1px solid #3e3d3d;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.main-logo {
  width: 20vh;
  height: 4vh;
}

#mobile-menu-icon {
  width: inherit;
  height: inherit;
}

#mobile-menu-btn {
  height: 4vh;
}

#desktop-menu-ul {
  display: none;
}

.top-nav-anchor {
