}

.shop-link-and-arrow {
  width: 20vw;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  display: flex;
  position: relative;
  bottom: 1vw;
}

.arrow-right {
  width: 3vw;
  height: 1vh;
  position: relative;
  left: 8%;
}

.category-btn-and-shop-link {
