  display: flex;
}

.form-field input:focus {
  border: 1.5px solid var(--main-orange);
  caret-color: var(--main-orange);
  outline: none;
}

.radio-btn-container {
  height: 6vh;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  margin-top: 3vh;
  display: flex;
}

.radio-btn-container:hover {
  border: 1.5px var(--main-orange) solid;
}

.radio-btn-label {
  padding-left: 8vw;
}

.radio-btn-label .payment-method-inputs {
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  height: 3vh;
  width: 3vh;
  background-color: #eee;
  border-radius: 50%;
  position: relative;
  top: 0;
  left: 0;
}

.checkmark:after {
  content: "";
  display: none;
  position: absolute;
}

.radio-btn-label .payment-method-inputs:checked ~ .checkmark:after {
  background-color: var(--main-orange);
  width: 1.7vh;
  height: 1.7vh;
  display: block;
  position: relative;
  top: 19%;
  left: 19%;
}

.radio-btn-label .checkmark:after {
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  top: 9px;
  left: 9px;
}

.radio-btn-label {
  flex-direction: row-reverse;
  align-items: center;
  gap: 5vw;
  display: flex;
  position: relative;
}

.radio-btn-label .payment-method-inputs ~ .checkmark {
  background-color: var(--pure-white);
  border: 1px solid #999;
}

.radio-btn-label:hover .payment-method-inputs ~ .checkmark {
  border: 1px solid #999;
}

