  background-color: var(--main-orange);
  border-radius: 50%;
  margin-top: 4vh;
  position: relative;
  top: 4%;
  left: 11%;
}

#tick-mark {
  width: 30px;
  height: 30px;
  display: inline-block;
  position: relative;
  top: 8%;
  left: 21%;
}

#tick-mark:before {
  height: 50%;
  width: 3px;
  background-color: var(--pure-white);
  content: "";
  transform-origin: 0 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateX(10px)rotate(-45deg);
}

#tick-mark:after {
  height: 3px;
  width: 90%;
  background-color: var(--pure-white);
  content: "";
  transform-origin: 0 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateX(10px)rotate(-45deg);
}

